import React, {Component} from 'react';
import $ from "jquery";

class SubjectModal extends Component {

    subject_id = null;
    user_id = null;

    changeSubject(event) {
        this.subject_id = event.target.value;
        this.user_id = $("#edit-user-id").val();
    }

    sendRequestToAddSubject() {
        console.log('http://localhost:3010/api/admin/users/' + this.user_id + '/subjects/' + this.subject_id);
        $.ajax({
            type: "PUT",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/users/' + this.user_id + '/subjects/' + this.subject_id,
        }).done(function (result) {
            window.M.toast({html: "Новий предмет успішно додано до викладача"});
            // Update edit modal
            let subjects = '';
            result.subjects.forEach((item, i) => {
                subjects += `<option value="${item._id}">${item.groupNumber} - ${item.title}</option>`;
            });
            $("#subjects").html(subjects);
            window.edit_subjects = window.M.FormSelect.init($("#subjects"));
        }).catch(function () {
            window.M.toast({html: "Вибраний викладач, вже прив'язани до цього предмета"});
        })
    }

    render() {
        return (
            <div id="subject" className="modal edit" >
                <div className='z-depth-4 edit-block-title'>
                    <p className='center-align text-accent-3 edit-title'>Додати предмет до викладача</p>
                </div>
                <div className="row" style={{height: 300}}>
                    <div className="input-field col s8 offset-s2">
                        <i className="material-icons prefix">folder_shared</i>
                        <select onChange={this.changeSubject.bind(this)} name="select" id="subject-select">
                            <option value="teacher">Викладач</option>
                            <option value="student">Студент</option>
                            <option value="null">Невизначений</option>
                        </select>
                        <label>Виберіть статус</label>
                    </div>
                    <div className='col s2 input-field'>
                        <a onClick={this.sendRequestToAddSubject.bind(this)} className="btn-floating btn-large waves-effect waves-light green"><i
                            className="material-icons">add</i></a>
                    </div>
                </div>
            </div>
        )
    }
}

export default SubjectModal;