import React, {Component} from 'react';
import './EditModal.css';
import $ from "jquery";

class DetailModal extends Component {
    name = null;
    timeout = null;
    surname = null;
    surname_timeout = null;
    phone = null;
    phone_timeout = null;
    email = null;
    email_timeout = null;
    password = null;
    password_timeout = null;

    subjectDelete(event) {

        $.ajax({
            type: "DELETE",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/users/' + $("#edit-user-id").val() + '/subjects/' + $("#subjects").val() + '/',
        }).done(function (result) {

            // Update edit modal
            let subjects = '';
            result.subjects.forEach((item, i) => {
                subjects += `<option value="${item._id}">${item.groupNumber} - ${item.title}</option>`;
            });
            $("#subjects").html(subjects);
            window.edit_subjects = window.M.FormSelect.init($("#subjects"));
            console.log(result);
        })

    }

    openSubjectModal(event) {
        $.ajax({
            type: "GET",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/subjects/',
            dataType: 'json',
        }).done((result) => {
            let subjects = '<option value="">Виберіть предмет</option>';
            result.subjects.forEach((item, i) => {
                subjects += `<option value="${item._id}">${item.groupNumber} - ${item.title}</option>`;
            });
            $("#subject-select").html(subjects);
            window.modal_subjects = window.M.FormSelect.init($("#subject-select"));
        });
    };

    sendRequestToUpdate(data) {

        for (let key in data) {
            if (key === 'name' || key === 'surname') {
                $("#name-" + $("#edit-user-id").val()).text((this.name || $("#edit-name").val()) + " " + (this.surname || $("#edit-surname").val()))
            }
            if (key === 'email') {
                $("#email-" + $("#edit-user-id").val()).text((this.email || $("#edit-email").val()));
            }
        }

        $.ajax({
            type: "PATCH",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/users/' + $("#edit-user-id").val(),
            data: data
        }).done(function (result) {
            $("#succes-change-" + result.change).html("<span style='color:#4CAF50;'>assignment_turned_in</span>");
            setTimeout(function () {
                $("#succes-change-" + result.change).text("create");
            }, 3000);
        })

    }

    nameChange(event) {
        clearTimeout(this.timeout);
        this.name = event.target.value;
        this.timeout = setTimeout(function () {
            this.sendRequestToUpdate({'name': this.name});
        }.bind(this), 800);
    };

    surnameChange(event) {
        clearTimeout(this.surname_timeout);
        this.surname = event.target.value;
        this.surname_timeout = setTimeout(function () {
            this.sendRequestToUpdate({'surname': this.surname});
        }.bind(this), 800);
    };

    phoneChange(event) {
        clearTimeout(this.phone_timeout);
        this.phone = event.target.value;
        this.phone_timeout = setTimeout(function () {
            this.sendRequestToUpdate({'phone': this.phone});
        }.bind(this), 800);
    };

    emailChange(event) {
        clearTimeout(this.email_timeout);
        this.email = event.target.value;
        this.email_timeout = setTimeout(function () {
            this.sendRequestToUpdate({'email': this.email});
        }.bind(this), 800);
    };

    accessChange(event) {
        this.sendRequestToUpdate({'access': event.target.value});
        switch (event.target.value) {
            case 'teacher':
                $("#id-"+$("#edit-user-id").val()+" .material-icons.icon").html('school');
                $("#id-"+$("#edit-user-id").val()+" a.btn.waves-effect.btn-icon").removeClass('orange');
                $("#id-"+$("#edit-user-id").val()+" a.btn.waves-effect.btn-icon").addClass('green');
                break;
            case 'student':
                $("#id-"+$("#edit-user-id").val()+" .material-icons.icon").html('face');
                $("#id-"+$("#edit-user-id").val()+" a.btn.waves-effect.btn-icon").removeClass('orange');
                $("#id-"+$("#edit-user-id").val()+" a.btn.waves-effect.btn-icon").addClass('green');
                break;

            default:
                $("#id-"+$("#edit-user-id").val()+" .material-icons.icon").html('priority_high');
                $("#id-"+$("#edit-user-id").val()+" a.btn.waves-effect.btn-icon").removeClass('green');
                $("#id-"+$("#edit-user-id").val()+" a.btn.waves-effect.btn-icon").addClass('orange');
                break;
        }
    }

    passwordChange(event) {
        clearTimeout(this.password_timeout);
        this.password = event.target.value;
        this.password_timeout = setTimeout(function () {
            this.sendRequestToUpdate({'password': this.password});
        }.bind(this), 800);
    };

    render() {
        return (
            <div id="edit" className="modal edit">
                <div className='z-depth-4 edit-block-title'>
                    <p className='center-align text-accent-3 edit-title'>Вікно редагування даних</p>
                </div>
                <input type='hidden' id='edit-user-id'/>
                <div className="modal-content">
                    <div className="row">
                        <form className="col s12" id="auth-form">

                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">account_circle</i>
                                    <input onInput={this.nameChange.bind(this)} id="edit-name" type="text"
                                           className="validate update-name"/>
                                    <label className='edit-label' htmlFor="name">
                                        <span>Ім'я<b>*</b></span>
                                    </label>
                                </div>
                                <div className='col s2 input-field'>
                                    <i id='succes-change-name' className="small material-icons">create</i>
                                </div>
                            </div>
                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">assignment</i>
                                    <input onInput={this.surnameChange.bind(this)} id="edit-surname" type="text"
                                           className="validate update-surname"/>
                                    <label className='edit-label'
                                           htmlFor="surname"><span>Прізвище<b>*</b></span></label>
                                </div>
                                <div className='col s2 input-field'>
                                    <i id='succes-change-surname' className="small material-icons">create</i>
                                </div>
                            </div>
                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">email</i>
                                    <input onInput={this.emailChange.bind(this)} id="edit-email" name="email"
                                           type="text" className="validate update-email"/>
                                    <label className='edit-label' htmlFor="email"><span>Email <b>*</b></span></label>
                                </div>
                                <div className='col s2 input-field'>
                                    <i id='succes-change-email' className="small material-icons">create</i>
                                </div>
                            </div>
                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">phone</i>
                                    <input onInput={this.phoneChange.bind(this)} id="edit-phone" type="text"
                                           className="validate update-phone"
                                    />
                                    <label className='edit-label' htmlFor="phone"><span>Телефон</span></label>
                                </div>
                                <div className='col s2 input-field'>
                                    <i id='succes-change-phone' className="small material-icons">create</i>
                                </div>
                            </div>

                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">folder_shared</i>
                                    <select onChange={this.accessChange.bind(this)} name="select"
                                            className="edit-access">
                                        <option value="teacher">Викладач</option>
                                        <option value="student">Студент</option>
                                        <option value="null">Невизначений</option>
                                    </select>
                                    <label>Виберіть статус</label>
                                </div>
                                <div className='col s2 input-field'>
                                    <i id='succes-change-access' className="small material-icons">create</i>
                                </div>
                            </div>

                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">grid_on</i>
                                    <select name="new_subject" id="subjects">
                                        <option value="teacher">ООП</option>
                                        <option value="student">МАТЕМАТИКА</option>
                                        <option value="null">Хімія</option>
                                    </select>
                                    <label>Предмети</label>
                                </div>
                                <div className="input-field col s2">
                                    <a onClick={this.openSubjectModal.bind(this)}
                                       className='btn waves-effect green modal-trigger _add_new_subject'
                                       href="#subject">
                                        <i className="large  material-icons">add_circle</i>
                                    </a>
                                    <a onClick={this.subjectDelete.bind(this)}
                                       className='btn waves-effect red remove-subject'>
                                        <i className="large  material-icons">remove_circle</i>
                                    </a>
                                </div>
                            </div>

                            <div className="row">
                                <div className="input-field col s8 offset-s2">
                                    <i className="material-icons prefix">security</i>
                                    <input onInput={this.passwordChange.bind(this)} id="edit-password" type="text"
                                           className="validate update-password"/>
                                    <label htmlFor="password">Новий пароль <b>*</b></label>
                                </div>
                                <div className='col s2 input-field'>
                                    <i id='succes-change-password' className="small material-icons">create</i>
                                </div>
                            </div>
                            <div className="row">
                                <br/>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        )
    }
}

export default DetailModal;