import React, {Component} from 'react';
import $ from 'jquery';

class AddSubject extends Component{

    constructor(props){
        super(props);
        $.ajax({
            type: "GET",
            headers: {
                "Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",
            },
            url: window.domenName+'/api/admin/groups/',
        }).done((result) => {
            let groups = '<option class="white-text" value="">Виберіть групу в якій буде проводитись пара</option>';
            result.forEach((item, i) => {
                groups  += `<option class="white-text" value="${item._id}">${item.number}</option>`;
            });
            $("#groups-select").html(groups);
            window.modal_subjects = window.M.FormSelect.init($("#groups-select"));
            $(".select-dropdown").css({color:'white'});
        });
    }
    addNewSubject(event){
        event.preventDefault();
        var title = $("#subject_title").val();
        var groupID = $("#groups-select").val();

        $.ajax({
            type: "POST",
            method: "POST",
            headers: {
                "Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",
            },
            data: {
                    groupID: groupID,
                    title: title
            },
            url: window.domenName+'/api/admin/subject/',
        }).done((result) => {
            if (result.success){
                window.M.toast({html: result.msg})
            }else{
                window.M.toast({html: result.msg})
            }
        });
    }

    render(){
        return (
            <div className='container'>
                <div className="row">
                    <div className="col s12 center-align z-depth-2">
                        <h3>Додати новий предмет</h3>
                    </div>
                </div>
                <div className="row">
                    <form className="col s12" id="auth-form">
                        <div className="row">

                            <div className="input-field col s4 offset-s2">
                                <i className="material-icons prefix">assignment</i>
                                <input style={{'color': 'white'}} id="subject_title" type="text" className="validate register-surname "/>
                                    <label htmlFor="subject_title">Назва предмета<b>*</b></label>
                            </div>

                            <div className="input-field col s4">
                                <i className="material-icons prefix">grid_on</i>
                                <select className='white-text'  name="group_id" id="groups-select">
                                </select>
                                <label>Група в якій буде проводитися</label>
                            </div>
                        </div>

                        <div className="row">
                            <button onClick={this.addNewSubject} className=" col s8 offset-s2 waves-effect waves-light btn"
                                    id="subject-action">Зберегти
                            </button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default AddSubject;
