import React, {Component} from 'react'
import $ from "jquery";

class Field extends Component {

    constructor(props) {
        super(props);
        this.state = {};
    }

    getRandomInt(max) {
        return Math.floor(Math.random() * Math.floor(max));
    }

    openDetailModal(userID, event) {
        $.ajax({
            type: "GET",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/users/' + userID,
            dataType: 'json',
        }).done((result) => {
            console.log(result);
            $("#detail-name").val(result.user.name);
            $("#detail-surname").val(result.user.surname);
            $("#detail-email").val(result.user.email);
            $("#detail-phone").val(result.user.phone);
            let element;
            if (result.user.access === 'teacher') {
                element = '<a class="btn waves-effect green"><i class="large  material-icons">school</i></a>';
            } else if (result.user.access === 'student') {
                element = '<a class="btn waves-effect green"><i class="large  material-icons">face</i></a>';
            } else {
                element = 'Cтатус <a class="btn waves-effect orange"><i class="large  material-icons">priority_high</i></a>';
            }
            $(".detail-access").html(element);

            let subjects = '';
            const colors = ['yellow darken-4', 'green accent-4', 'cyan accent-4', 'light-blue accent-4', 'teal darken-3', 'purple darken-3', 'blue-grey darken-2'];
            result.subjects.forEach((item, i) => {
                subjects += `<div class='col s2'>
                                <a class="waves-effect ${colors[this.getRandomInt(6)]} waves-light btn-small">${item.groupNumber} ${item.title}</a>
                            </div>`;
            });
            $(".detail-subjects").html(subjects);
        });
    };

    openEditModal(userID, event) {
        $.ajax({
            type: "GET",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/users/' + userID,
            dataType: 'json',
        }).done((result) => {
            console.log(result);
            $("#edit-name").val(result.user.name);
            $("#edit-name").focus();
            $("#edit-surname").val(result.user.surname);
            $("#edit-surname").focus();
            $("#edit-email").val(result.user.email);
            $("#edit-email").focus();
            $("#edit-phone").val(result.user.phone);
            $("#edit-phone").focus();
            let element;

            if (result.user.access === 'teacher') {
                element = `<option value="teacher">Викладач</option>
                <option value = "student" >Студент</option>
                <option value="null">Невизначений</option>`
            } else if (result.user.access === 'student') {
                element = `<option value="student">Студент</option>
                <option value = "teacher" >Викладач</option>
                <option value="null">Невизначений</option>`
            } else {
                element = `<option value="null">Невизначений</option>
                <option value = "teacher" >Викладач</option>
                <option value="student">Студент</option>`
            }
            $(".edit-access").html(element);
            window.edit_select = window.M.FormSelect.init($(".edit-access"));

            let subjects = '';
            result.subjects.forEach((item, i) => {
                subjects += `<option value="${item._id}">${item.groupNumber} - ${item.title}</option>`;
            });
            $("#subjects").html(subjects);
            window.edit_subjects = window.M.FormSelect.init($("#subjects"));
            $("#edit-user-id").val(result.user._id)
        });
    };

    setDeleteUserID(userID, event){
        window.deleteUserID = userID;
        $(".title-del-modal").html('Видалити? '+ '<span style="text-decoration: underline">'+$("#name-"+userID).text()+'</span>');
    }

    render() {
        return (
            <tr key={this.props.user._id} id={'id-'+this.props.user._id}>
                <td>
                            {(() => {
                                switch (this.props.user.access) {
                                    case "teacher":   return  <a className="btn btn-icon waves-effect green">
                                        <i className="large icon  material-icons">school</i>
                                    </a>;
                                    case "student": return <a className="btn btn-icon waves-effect green">
                                        <i className="large icon material-icons">face</i>
                                    </a>;
                                    default: return <a className="btn btn-icon waves-effect orange">
                                        <i className="large icon material-icons">priority_high</i>
                                    </a>;

                                }
                            })()}
                </td>
                <td id={'name-'+this.props.user._id}>{this.props.user.name} {this.props.user.surname}</td>
                <td id={'email-'+this.props.user._id}>
                    {this.props.user.email}
                </td>
                <td>

                    <a onClick={this.openDetailModal.bind(this, this.props.user._id)}
                       className="btn waves-effect indigo modal-trigger" data-id="5c82cc139252170990e1fdc6"
                       href="#detail">
                        <i className="large  material-icons">assignment</i>
                    </a>
                    <a onClick={this.openEditModal.bind(this, this.props.user._id)}
                       className="btn waves-effect green modal-trigger user-edit"
                       id="5c82cc139252170990e1fdc6" href="#edit">
                        <i className="large  material-icons">mode_edit</i>
                    </a>
                    <a onClick={this.setDeleteUserID.bind(this, this.props.user._id)} href="#user-delete"
                       className="btn waves-effect red modal-trigger">
                        <i className="large  material-icons">delete_forever</i>
                    </a>
                </td>
            </tr>
        )
    }
}

export default Field;