import React, {Component} from 'react';
import $ from 'jquery';
import './index.css';

class AddUser extends Component {

    constructor(props) {
        super(props);

    }

    componentDidMount() {
        window.M.FormSelect.init($("#access"));

    }

    addNewUser = (event) => {
        event.preventDefault();
        console.log(window.domenName);
        $.ajax({
            type: "POST",
            method: "POST",
            headers: {
                "Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",
            },
            data: {
                email: this.email.value,
                password: this.password.value,
                name: this.name.value,
                surname: this.surname.value,
                phone: this.phone.value,
                access: this.access.value
            },
            url: window.domenName + '/api/admin/users/',
        }).done((result) => {
                window.M.toast({html: 'Користувач ' + result.user.name + result.user.surname +' успішно створено'});
        }).catch(()=>{
            window.M.toast({html: 'Користувач з такою поштою вже зареєстрований'});
        })
    };

    render() {
        return (
            <div className='container add-user-form'>
                <div className="row">
                    <div className="col s12 center-align z-depth-2">
                        <h3>Додати нового користувача</h3>
                    </div>
                </div>
                <div className="row">
                    <form className="col s9 offset-s2" id="auth-form">

                        <div className="row">
                            <div className="input-field col s8 offset-s2">
                                <i className="material-icons prefix">account_circle</i>
                                <input id="name" ref={input => {
                                    this.name = input
                                }} type="text" className="validate update-name"/>
                                <label className='edit-label' htmlFor="name">
                                    <span>Ім'я<b>*</b></span>
                                </label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s8 offset-s2">
                                <i className="material-icons prefix">assignment</i>
                                <input id="surname" type="text" ref={input => {
                                    this.surname = input
                                }}
                                       className="validate update-surname"/>
                                <label className='edit-label'
                                       htmlFor="surname"><span>Прізвище<b>*</b></span></label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s8 offset-s2">
                                <i className="material-icons prefix">email</i>
                                <input id="email" name="email" ref={input => {
                                    this.email = input
                                }}
                                       type="text" className="validate update-email"/>
                                <label className='edit-label' htmlFor="email"><span>Email <b>*</b></span></label>
                            </div>
                        </div>
                        <div className="row">
                            <div className="input-field col s8 offset-s2">
                                <i className="material-icons prefix">phone</i>
                                <input id="phone" type="text" ref={input => {
                                    this.phone = input
                                }}
                                       className="validate update-phone"/>
                                <label className='edit-label' htmlFor="phone"><span>Телефон</span></label>
                            </div>
                        </div>

                        <div className="row">
                            <div className="input-field col s8 offset-s2">
                                <i className="material-icons prefix">folder_shared</i>
                                <select name="select" ref={input => {
                                    this.access = input
                                }} id="access" style={{color: 'white'}}>
                                    <option value="teacher">Викладач</option>
                                    <option value="student">Студент</option>
                                    <option value="null">Невизначений</option>
                                </select>
                                <label>Виберіть статус</label>
                            </div>
                        </div>

                        <div className="row">
                            <div className="input-field col s8 offset-s2">
                                <i className="material-icons prefix">security</i>
                                <input id="edit-password" type="text" ref={input => {
                                    this.password = input
                                }}
                                       className="validate update-password"/>
                                <label htmlFor="password">Пароль <b>*</b></label>
                            </div>

                        </div>
                        <div className="row">
                            <br/>
                            <button onClick={this.addNewUser} className='col s12 btn'>Зареєструвати</button>
                        </div>
                    </form>
                </div>
            </div>
        )
    }
}

export default AddUser;
