import React, {Component} from 'react'
import $ from 'jquery';
import Field from './Field';
import DetailModal from './EditModal';
import EditModal from './DetailModal';
import SubjectModal from './SubjectModal';
import DeleteModal from './DeleteModal';

class Table extends Component {
    constructor(props) {
        super(props);

        this.state = {};
    }
    componentDidMount(){
        $.ajax({
            type: "GET",
            headers: {
                "Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",
            },
            url: window.domenName+'/api/admin/users/',
            dataType: 'json',
        }).done((result) => {
            console.log(result);
            this.setState({users: result});
        });
        document.addEventListener('DOMContentLoaded', function() {
            const modal = document.querySelectorAll('.modal');
            window.M.Modal.init(modal);

        });
    }

    render() {
        return (<div className="row">
                <div className="col s12">
                    <table className="centered highlight responsive-table">
                        <thead>
                        <tr>
                            <th>Cтатус</th>
                            <th>Особисті дані</th>
                            <th>Email</th>
                            <th>Дії</th>
                        </tr>
                        </thead>
                        <tbody>
                        {this.state.users === undefined || this.state.users.map((value, index) => {
                            return <Field key={value._id} user={value}/>
                        })}
                        </tbody>
                    </table>
                </div>
                <DetailModal/>
                <EditModal/>
                <SubjectModal/>
                <DeleteModal/>
            </div>
        )
    }
}

export default Table;