import React, {Component} from 'react';
import './DetailModal.css';

class EditModal extends Component {

    render() {
        return (
            <div id="detail" className="modal bottom-sheet">
                <div className="modal-content">
                    <div className="row">
                        <div className="col s12">
                            <div className="col s3">
                                <label>Ім'я</label>
                                <input id='detail-name' readOnly={true}/>
                            </div>
                            <div className="col s3">
                                <label>Прізвище</label>
                                <input id='detail-surname' readOnly={true}/>
                            </div>
                            <div className="col s3">
                                <label>Телефон</label>
                                <input id='detail-phone' readOnly={true}/>
                            </div>
                            <div className="col s3">
                                <label>Пошта</label>
                                <input id='detail-email' readOnly={true}/>
                            </div>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col s3 detail-access"> </div>
                        <div className="col s9 detail-subjects">

                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default EditModal;