import React, {Component} from 'react';
import './Header.css'

class Header extends Component {

    constructor(props) {
        super(props);
        this.state = {};
        document.addEventListener('DOMContentLoaded', function() {
            var elems = document.querySelectorAll('.sidenav');
            var instances = window.M.Sidenav.init(elems);
        });

    }

    render() {
        return (
            <header>
                <div className="row  purple darken-3" style={{ "marginBottom": '0'}}>
                    <div className="col s12 center-align">
                        <a href="#" data-target="slide-out" className="sidenav-trigger brand-logo center"><i
                            className=" medium material-icons">dashboard</i></a>
                    </div>
                </div>

                <ul id="slide-out" className="sidenav">
                    <li><br/><div className="divider"></div></li>
                    <li><a className="subheader center-align">БД</a></li>
                    <li><a href="/admin"><i className="material-icons">cloud</i>База даних</a></li>
                    <div className="divider"></div>
                    <li><a className="subheader center-align">Дії</a></li>
                    <li><a href="/admin/subject"><i className="material-icons">add_circle_outline</i>Додати предмет</a></li>
                    <li><a href="/admin/group"><i className="material-icons">add_circle_outline</i>Додати групу</a></li>
                    <li><a href="/admin/user"><i className="material-icons">add_circle_outline</i>Додати користувача</a></li>
                </ul>
                <div className={'head-block'}>
                    База даних
                </div>
            </header>
        );
    }
}

export default Header;
