import React, { Component } from 'react';
import $ from "jquery";

class AddGroup extends Component {
    number = null;
    number_timer = null;
    state = {};

    componentDidMount(){
        $.ajax({
            type: "GET",
            headers: {
                "Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",
            },
            url: window.domenName+'/api/admin/groups/',
        }).done((result) => {
            this.setState({groups: result});
        });
    }
    deleteGroup(id, event){
        $.ajax({
            type: "DELETE",
            headers: {
                "Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",
            },
            url: window.domenName+'/api/admin/groups/'+id,
        }).done((result) => {
            window.M.toast({html: "Групу успішно видалено"});
            this.setState({groups: result});
        });
    }
    sendRequest = (number, self) => {
        $.ajax({
            type: "POST",
            headers: {"Authorization": "Token eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJlbWFpbCI6ImZva2tpbWlua292MkBnbWFpbC5jb20iLCJpZCI6IjVjODdiNDk0MzA2Y2ViMTg1MDdhOGU5MSIsImV4cCI6MTU1NzU4MTQ2MCwiaWF0IjoxNTUyMzk3NDYwfQ.WCmE_VO4oloHL9QS_BzGgpaSt97FdEtSbYVR7T1NEXw",},
            url: window.domenName+'/api/admin/groups/',
            data: {
                group: number
            }
        }).done(function (result) {
            if (result.success){
                window.M.toast({html: result.msg});
                self.setState({groups: result.groups});
            }else{
                window.M.toast({html: result.msg})
            }
        })
    };

    groupNumber(event){
        clearTimeout(this.number_timer);
        this.number = event.target.value;
        this.number_timer = setTimeout(function () {
            this.sendRequest(this.number, this);
        }.bind(this), 1000);
    }

    render() {
        return (
            <div className='container'>
                <div className="row">
                    <div className="col s12 center-align z-depth-2">
                        <h3>Додати нову групу</h3>
                    </div>
                </div>
                <div className="row">
                    <form className="col s12" id="auth-form">
                        <div className="row">

                            <div className="input-field col s6 offset-s3">
                                <i className="material-icons prefix">assignment</i>
                                <input onInput={this.groupNumber.bind(this)}
                                       id="number_group" type="number" style={{ 'color': 'white'}}
                                       className="validate register-surname center-align"/>
                                <label htmlFor="number_group">Номер групи</label>
                            </div>
                        </div>
                        <hr/>
                        <h6 className='center-align'>Групи які вже зареєстровано</h6>
                        <br/>
                        <div className="row">
                            {this.state.groups === undefined || this.state.groups.map((value, index) => {
                                return <div className="btn purple col s1 offset-s1" key={value._id} style={{'margin-bottom': '25px'}}>
                                        <div className='row'>
                                        <span className='col s7' style={{'font-size': '24px'}}>{value.number}</span>
                                        <span onClick={this.deleteGroup.bind(this, value._id)}
                                            className='group-delete col s3'><i className="center-align tiny material-icons">close</i></span>
                                    </div>
                                </div>
                            })}
                        </div>
                    </form>
                </div>
            </div>
        );
    }
}

export default AddGroup;
